export default {
	data() {
		return {
			userid: '',
			houselist: [],
			imgSrc: []
		}
	},
	onLoad() {
		console.log(111)
		// uni.loadFontFace({
		// 	family: 'yueyuan',
		// 	// 本地字体路径需转换为平台绝对路径
		// 	source: `url(${plus.io.convertLocalFileSystemURL('_www/static/yueyuan.otf')})`,
		// 	success() {
		// 	},
		// 	fail(e) {
		// 		console.log('fail')
		// 	}
		// })
		var _this = this
		this.userid = this.$store.state.userid
		console.log(this.userid)
		// this.userid = 176713343012
		console.log(this.$store.state.userid)
		uni.request({
			url: this.$baseUrl.baseUrl + '/getmyHouse',
			data: {
				userid: this.userid
			},
			success: (res) => {
				console.log(res.data.data)
				_this.houselist = res.data.data
				for(var i=0;i<_this.houselist.length;i++){
					_this.imgSrc.push(_this.$baseUrl.baseUrl + _this.houselist[i].newProductimg[0].newimgSrc)	
				}				
			}
		})
	},
	methods: {
		del(index){
			var _this = this
			console.log("删除第" + index + "条数据")
			var producid = this.houselist[index].productid
			uni.showModal({
				title: "提示",
				content: "确定要删除这条数据吗？",
				showCancel: true,
				success: function(e){
					console.log(e)
					if(e.confirm){
						uni.request({
							url: _this.$baseUrl.baseUrl + '/deleteProduct',
							data: {
								productid: producid
							},
							success: (res) => {
								console.log(res)
							}
						})
					}
				}
			})
		}
	}
}
